html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.podium-container {
  position: fixed;
  opacity: 0.01;
  width: 1px;
  height: 1px;
  left: -1000px;
  top: -1000px;
}

.podium {
  position: relative;
  width: 400px;
}

#sel_channel_wrapper {
  display: none;
}

#sel_system_wrapper,
#sel_area_wrapper,
#sel_role_wrapper {
  width: 100%;
  height: 90px;
  padding: 16px 20px;
  margin: 0;

  font-size: 22pt;

  background-color: #fed363;
  border: 2px solid #000000;
  border-radius: 8px;
  margin-bottom: 20px;

  display: flex !important;
  align-items: center;

  box-shadow: 3px 4px #000000;
}

.podium select {
  appearance: none;
  width: 100%;
  height: 100%;
  background-color: transparent;
  padding: 0 16px;
  font-size: inherit;
  border: 0;
  border-left: 1px solid;
  border-radius: 0;
  outline: none;
  text-align-last: center;
}

#sel_system_wrapper::before,
#sel_area_wrapper::before,
#sel_role_wrapper::before {
  display: block;
  flex-shrink: 0;
  font-size: inherit;
  padding-right: 16px;
}

#sel_system_wrapper::after,
#sel_area_wrapper::after,
#sel_role_wrapper::after {
  display: block;
  flex-shrink: 0;
  content: ' ';
  width: 16px;
  height: 13px;
  background: url(data:image/png;base64,)
    no-repeat;
}

#sel_system_wrapper::before {
  content: '系统';
}

#sel_area_wrapper::before {
  content: '大区';
}

#sel_role_wrapper::before {
  content: '角色';
}

.podium #btn_submit {
  position: absolute;
  left: 330px;
  top: 560px;
  transform: translateX(-50%);

  width: 220px;
  height: 76px;
}
